import d from './data.json';
import webpImages from '../images/crew/*.webp';
import pngImages from '../images/crew/*.png';


function main() {

    const nav = document.querySelectorAll('.crew__nav__link');
    const subtitle__number = document.querySelector('.crew__subtitle--number');
    const commander = document.querySelector('.crew__title--commander');
    const name = document.querySelector('.crew__title--name');
    const bio = document.getElementById('crew__bio');
    const picture = document.getElementById('crew__image');
    const image = document.querySelector('.crew__image');
    let data = d.crew;

    const crew = {
        '01': 0,
        '02': 1,
        '03': 2,
        '04': 3,
    }

    nav.forEach(elm => {
        elm.addEventListener('click', handelNav);
    })


    function handelNav(e) {
        nav.forEach(elm => {
            elm.classList.remove('active');
        });
        image.classList.add('zoomIn');
        name.classList.add('fadeIn');
        commander.classList.add('fadeIn');
        bio.classList.add('fadeIn');
        setTimeout(() => {
            image.classList.remove('zoomIn');
            name.classList.remove('fadeIn');
            commander.classList.remove('fadeIn');
            bio.classList.remove('fadeIn');
        }, 700)

        e.target.classList.add('active');
        let number = e.target.dataset.id;
        const current = crew[number];
        subtitle__number.textContent = number;
        name.textContent = data[current].name;
        commander.textContent = data[current].role;
        bio.textContent = data[current].bio;
        handleImage('/', data[current].images.webp, data[current].images.png, picture);

    }

    function handleImage(path, webp, png, picture) {
        picture.children[0].srcset = webpImages[webp];
        picture.children[1].srcset = pngImages[png];
        picture.children[2].src = path + png;
    }


}

window.addEventListener('DOMContentLoaded', main);